<template>
    <div>
        <el-header class="header">
            <el-button type="primary" plain @click="showDialog">+ 新增</el-button>
            <el-input class="input" placeholder="请输入内容" v-model="input" clearable></el-input>
            <el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        </el-header>

        <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="30%">
            <el-form :model="formData" ref="formData" label-width="80px">
                <el-form-item label="日期" prop="date">
                    <el-date-picker v-model="formData.date" type="date" placeholder="选择日期"
                        :picker-options="pickerOptions"></el-date-picker>
                </el-form-item>
                <el-form-item label="姓名" prop="name">
                    <el-input v-model="formData.name" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                    <el-radio-group v-model="formData.sex">
                        <el-radio label="男">男</el-radio>
                        <el-radio label="女">女</el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="省份" prop="province">
                    <el-select v-model="formData.province" placeholder="请选择省份">
                        <el-option v-for="province in provinces" :key="province" :label="province" :value="province">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="市区" prop="city">
                    <el-select v-model="formData.city" placeholder="请选择市区">
                        <el-option v-for="city in cities[formData.province]" :key="city" :label="city" :value="city">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="地址" prop="address">
                    <el-input v-model="formData.address" placeholder="请输入地址"></el-input>
                </el-form-item>
                <el-form-item label="邮编" prop="zip">
                    <el-input v-model="formData.zip" placeholder="请输入邮编"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleCancel">取 消</el-button>
                <el-button type="primary" @click="saveData">{{ editingIndex === null ? '新增' : '保存' }}</el-button>
            </span>
        </el-dialog>

        <el-table :data="showTableData" border style="width: 100%">
            <el-table-column prop="date" label="日期"></el-table-column>
            <el-table-column prop="name" label="姓名"></el-table-column>
            <el-table-column prop="sex" label="性别"></el-table-column>
            <el-table-column prop="province" label="省份"></el-table-column>
            <el-table-column prop="city" label="市区"></el-table-column>
            <el-table-column prop="address" label="地址"></el-table-column>
            <el-table-column prop="zip" label="邮编"></el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="editData(scope.row)"
                        style="margin-left: 10px;">编辑</el-button>
                    <el-button type="danger" size="mini" @click="confirmDelete(scope.row)"
                        style="margin-left: 10px">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination layout="total, sizes, prev, pager, next, jumper" @size-change="handleSizeChange"
            @current-change="handleCurrentChange" :current-page="pageIndex" :page-size="pageSize"
            :page-sizes="[2, 4, 6, 10]" :total="total">
        </el-pagination>
    </div>
</template>

