        },
        methods: {
            // 获取用户信息列表
            getUserInformation() {
                axios.get('http://localhost:8080/get-user-information')
                    .then(response => {
                        this.tableData = response.data;
                        this.total = this.tableData.length;
                        this.getShowTableData();
                    })
                    .catch(error => {
                        console.error('获取用户信息失败:', error);
                    });
            },
            // 显示新增/编辑数据对话框
            showDialog() {
                this.dialogTitle = '新增数据';
                this.dialogVisible = true;
                // 重置表单数据和编辑索引
                this.formData = {
                    date: '',
                    name: '',
                    sex: '',
                    province: '',
                    city: '',
                    address: '',
                    zip: ''
                };
                this.editingIndex = null;
            },
            // 保存或编辑数据
            saveData() {
                // 提取日期部分
                // const formattedDate = new Date(this.formData.date).toISOString().split('T')[0];
                // this.formData.date = formattedDate;
                // 从表单中获取日期字符串
                const dateString = this.formData.date;

                // 创建一个新的 Date 对象，使用输入的日期字符串
                const dateObject = new Date(dateString);

                // 提取日期部分（年、月、日），不包括时分秒
                const year = dateObject.getFullYear();
                const month = dateObject.getMonth() + 1; // 月份从0开始，需要加1
                const day = dateObject.getDate();

                // 格式化成 YYYY-MM-DD 的形式
                const formattedDate = `${year}-${month < 10 ? '0' + month : month}-${day < 10 ? '0' + day : day}`;

                // 更新表单中的日期值
                this.formData.date = formattedDate;
                if (this.editingIndex === null) {
                    // 新增数据
                    axios.post('http://localhost:8080/save-user-information', this.formData)
                        .then(response => {
                            console.log('数据新增成功:', response.data);
                            this.getUserInformation();
                            this.dialogVisible = false;
                        })
                        .catch(error => {
                            console.error('新增数据失败:', error);
                        });
                } else {
                    // 编辑现有数据
                    axios.put(`http://localhost:8080/update-user-information/${this.tableData[this.editingIndex].id}`, this.formData)
                        .then(response => {
                            console.log('数据编辑成功:', response.data);
                            this.getUserInformation();
                            this.dialogVisible = false;
                        })
                        .catch(error => {
                            console.error('编辑数据失败:', error);
                        });
                }
            },
            // 编辑数据
            editData(row) {
                // 填充表单数据
                this.formData = { ...row };
                // 设置编辑索引
                this.editingIndex = this.tableData.indexOf(row);
                // 显示对话框
                this.dialogTitle = '编辑数据';
                this.dialogVisible = true;
            },
            // 确认删除
            confirmDelete(row) {
                this.$confirm('此操作将永久删除该数据，是否继续？', '警告', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    // 如果确认，执行删除操作
                    if (this.showTableData.length === 1 && this.pageIndex > 1)
                        this.pageIndex--;
                    this.deleteData(row);
                }).catch(() => {
                    // 如果取消，不执行任何操作
                });
            },
            // 删除数据
            deleteData(row) {
                axios.delete(`http://localhost:8080/delete-user-information/${row.id}`)
                    .then(response => {
                        console.log('数据删除成功:', response.data);
                        this.getUserInformation();
                        this.getShowTableData();
                    })
                    .catch(error => {
                        console.error('删除数据失败:', error);
                    });
            },
            // 搜索用户信息
            search() {
                axios.get('http://localhost:8080/search-user-information', {
                    params: {
                        keyword: this.input
                    }
                })
                    .then(response => {
                        this.tableData = response.data;
                        this.getShowTableData();
                    })
                    .catch(error => {
                        console.error('搜索失败:', error);
                    });
            },
            // 取消新增/编辑操作
            handleCancel() {
                this.dialogVisible = false;
                // 重置表单数据和编辑索引
                this.formData = {
                    date: '',
                    name: '',
                    sex: '',
                    province: '',
                    city: '',
                    address: '',
                    zip: ''
                };
                this.editingIndex = null;
            },
            getShowTableData() {
                // 5. 获取截取开始索引
                let begin = (this.pageIndex - 1) * this.pageSize;
                // 6. 获取截取结束索引
                let end = this.pageIndex * this.pageSize;
                // 7. 通过索引去截取，从而展示
                this.showTableData = this.tableData.slice(begin, end);
            },
            // 8. 页数改变，重新截取
            handleCurrentChange(val) {
                this.pageIndex = val;
                this.getShowTableData();
            },
            // 9. 条目数改变，也重新截取
            handleSizeChange(val) {
                this.pageIndex = 1;
                this.pageSize = val;
                this.getShowTableData();
            }
        },
        mounted() {
            this.getUserInformation();
        }
    };
</script>

<style>
    .header {
        display: flex;
        align-items: center;
    }

    .input {
        width: 200px;
        margin-left: auto;
    }
</style>